<template>
  <div
    class="avatar"
    :style="{
      width: `${props.size}px`,
      height: `${props.size}px`,
      backgroundImage: `url(${props.src})`,
    }"
  ></div>
</template>

<style scoped>
.avatar {
  border-radius: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
</style>

<script>
export default {
  name: "Avatar",
  props: ["src", "size"],

  setup(props) {
    return { props };
  },
};
</script>
